import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate, Link } from "react-router-dom";
import { FacebookShareButton, FacebookIcon, WhatsappShareButton, WhatsappIcon, TelegramShareButton, TelegramIcon, TwitterIcon, TwitterShareButton, FacebookMessengerShareButton, FacebookMessengerIcon, LineShareButton, LinkedinIcon, PinterestIcon, PinterestShareButton, LinkedinShareButton } from 'react-share'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import '../style/textposts.css';
import '../style/blog.css';

const Textposts = () => {
  const [data, setData] = useState([]);
  const [comment, setComment] = useState('');
  const [cblogid, setblogid] = useState(false);
  const [commentdata, setcommentdata] = useState([]);
  const navigate = useNavigate();
  const [likeid, setlike] = useState('')
  const [Loading, setloading] = useState(true)
  const [sharedata, setsharedata] = useState('')
  const [search, setsrch] = useState('')

  // login check karta hai
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        console.log(response.data.user);
        if (response.data.user === "error") {
          navigate('/', { replace: true })


        }

      } catch (error) {
        toast.error('Something is Wrong!', {
          position: "bottom-center"
      })
      }

    }
    getUser()


  }


    , [])
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get(`/textsposts`)
        setData(response.data.post)
        console.log(response.data.post)
        setloading(false)
        setlike(response.data.userli)
        localStorage.setItem("id", response.data.userli)
        console.log(likeid + " fghj")
        setblogid(false)
      } catch (error) {
        toast.error('Something is Wrong!', {
          position: "bottom-center"
      })
      }

    }
    getUser()


  }


    , [cblogid])



  async function getUser(id) {
    try {
      const response = await axios.get(`/comment/${id}`)
      console.log(response.data.usercomment);
      setcommentdata(response.data.usercomment)

    } catch (error) {
      console.error(error);
    }

  }
  let commentsub = async (blogid) => {
    console.log(blogid + "AA")
    if (blogid === undefined) {
      toast.success('Comment submiited', {
        position: "bottom-center"
    })
      console.log(blogid)
    }else if(comment === '') {
      toast.warn('plaese type comment', {
        position: "top-center"})
    } else {
      try {

        await axios.post(`/commenttext/${blogid}`, {
          content: comment
        }

        )

      } catch (error) {
        toast.error('Something is Wrong!', {
          position: "bottom-center"
      })
      }
    }
  }
  // const copyurl = (posturl) => {
  //   navigator.clipboard.writeText(posturl)
  //   toast.success('Copied!', {
  //     position: "top-center"})
       

  // }
  let commentdo = async (getblogid) => {

    console.log(getblogid)


    getUser(getblogid)

  }




  const likeblog = async (userid) => {
    const response = await axios.get(`/likeTpost/${userid}`)


    console.log(response.data.user)
    if (response.data.user === "Like") {
      localStorage.setItem("id", likeid)
      // navigate('/blogs', { replace: true })
      setblogid(true)

      toast('Liked', {
        position: "bottom-center"
    })
    } else {
      toast.error('Something is Wrong!', {
        position: "bottom-center"
    })
    }

  }
  const unlikeblog = async (userid) => {
    const response = await axios.get(`/unlikeTpost/${userid}`)


    console.log(response.data.user)
    if (response.data.user === "unlike") {
      console.log("sucess")
      localStorage.setItem("id", "vdfvdfdf")
      // navigate('/blogs', { replace: true })
      setblogid(true)
      toast('Unliked', {
        position: "bottom-center"
    })

    } else {
      toast.error('Something is Wrong!', {
        position: "bottom-center"
    })
    }

  }
  const sharepost = (blogid) => {
    setsharedata(blogid)
  }
  useEffect(() => {
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
    })
    
    });
    
    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
    })

    });
    
  },[])


  const Isloading = () => {
    return (
      <>
        <div className="bubblingG">
          <span id="bubblingG_1" />
          <span id="bubblingG_2" />
          <span id="bubblingG_3" />
        </div>

      </>
    )
  }
  const Datanull = () => {
    return (
      <h1>Not Found</h1>
    )
  }

  const Data = () => {
    return (
      <>
      </>
    )
  }
  const RData = () => {
    return (
      <>
        {data.length === 0 ? <Datanull /> : <Data />}

      </>
    )
  }
  const searchblogs = data.filter((item) => {
    if (search === "") {
      return item
    }
    else if (item.question.toLowerCase().includes(search.toLocaleLowerCase())) {
      return item
    }
  })


  return (
    <>
      {Loading ? <Isloading /> : (<>
      <div className='textposts'>
        <div style={{marginTop:'10px'}}>
        <div class="input-group">
                <div class="form-outline">
                  <input type="search" id="form1" class="form-control"  name='search' onChange={(e) => { setsrch(e.target.value) }} placeholder='Posts title' style={{width:'350px',marginLeft:'5px'}} required />
                  <label class="form-label" for="form1">Search Posts</label>
                </div>
              </div>




          <div className="App">





            {searchblogs.map((value) => (
              <div className="w3-card-4" style={{ width: "360px",marginBottom:'20px',border:'1px dashed red' }} key={value._id} >

                <header className="w3-container" style={{backgroundColor:'rgb(209 249 151)'}} >
                  <h3>{value.username}</h3>
                </header>
                <Link  to={`/textpostview/${value._id}`} style={{textDecoration: 'none'}} activeClassName="current">
                  <div className="w3-container" style={{marginBottom:'20px'}}>

                    <img
                      src={value.userimage}
                      alt="Avatar"
                      className="w3-left w3-circle w3-margin-right"
                      style={{ width: '60px',height:'60px'}}
                    />
                    <b style={{fontSize:'large',color:'black'}}>
                      {value.question}
                    </b>
                    <br />
                  </div>
                  
                  </Link>
                  
                  < div style={{marginLeft:'35px'}}>
                {<div onClick={() => {
                  if (value.like.includes(localStorage.getItem("id"))) {
                    unlikeblog(value._id)

                  } else {
                    likeblog(value._id)
                    console.log("pass")

                  }
                }}>{value.like.includes(localStorage.getItem("id")) ? (<i className='fa fa-heart' style={{ color: "red",fontSize:'25px' }}></i>) : (<i className='fa fa-heart-o' style={{ fontSize:'25px' }}></i>)}</div>}

                <p>{value.like ? value.like.length : "0"} Likes</p>

                <div style={{display:'flex'}}>
            <input type='text' name='content' className='textinp' placeholder='type commet' onChange={(e) => { setComment(e.target.value) }} />
            <i onClick={() => { commentsub(value._id) }} className="fa fa-paper-plane-o" style={{fontSize:'25px'}}></i>
            </div>
                {/* <Link to={`/blogviews/${value._id}`} activeClassName="current"></Link> */}
                {/* yeh code direct connect hai ek funtion se */}
                <div style={{display:'flex'}}>
                <div style={{marginRight:"20px"}} data-bs-toggle="modal" data-bs-target="#staticBackdrop" onClick={() => { commentdo(value._id) }}>
                  <i class="fa fa-comment-o" aria-hidden="true" style={{ fontSize: "30px" }}></i>
                </div><hr/>



                <div data-bs-toggle="modal" data-bs-target="#share" onClick={() => { sharepost(value._id) }}>
                  <i className='fa fa-share' style={{ fontSize: "30px" }}></i>
                </div>
</div>
              </div>

</div>
            ))}
















          </div>
        </div>

        <div className="modal fade" id="share" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h1 className="modal-title fs-5" id="staticBackdropLabel">Share Box</h1>
                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <div className="modal-body">
                <FacebookShareButton url={`https://x6v4mq-5500.csb.app/textpostview/${sharedata}`} quote='Blog' hashtag='#AR'>
                  <FacebookIcon size={40} />
                </FacebookShareButton>
                <WhatsappShareButton url={`https://x6v4mq-5500.csb.app/textpostview/${sharedata}`} quote='Blog' hashtag='#AR'>
                  <WhatsappIcon size={40} />
                </WhatsappShareButton>
                <TelegramShareButton url={`https://x6v4mq-5500.csb.app/textpostview/${sharedata}`} quote='Blog' hashtag='#AR'>
                  <TelegramIcon size={40} />
                </TelegramShareButton>
                <PinterestShareButton url={`https://x6v4mq-5500.csb.app/textpostview/${sharedata}`} quote='Blog' hashtag='#AR'>
                  <PinterestIcon size={40} />
                </PinterestShareButton>
                <TwitterShareButton url={`https://x6v4mq-5500.csb.app/textpostview/${sharedata}`} quote='Blog' hashtag='#AR'>
                  <TwitterIcon size={40} />
                </TwitterShareButton>
                <FacebookMessengerShareButton url={`https://x6v4mq-5500.csb.app/textpostview/${sharedata}`} quote='Blog' hashtag='#AR'>
                  <FacebookMessengerIcon size={40} />
                </FacebookMessengerShareButton>
                <LinkedinShareButton url={`https://x6v4mq-5500.csb.app/textpostview/${sharedata}`} quote='Blog' hashtag='#AR'>
                  <LinkedinIcon size={40} />
                </LinkedinShareButton>
                {/* <i className='fa fa-clone' style={{fontSize:'40px'}} onClick={()=>{copyurl(`http://localhost:3000/blogview/${sharedata}`)}}></i> */}



              </div>
              {/* <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-primary">Save changes</button>
              </div> */}
            </div>
          </div>
        </div>




        <div className="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h1 className="modal-title fs-5" id="staticBackdropLabel">Comment Box</h1>
                <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close" />
              </div>
              <div className="modal-body">

                <div className="CApp">
                  {commentdata.map((comment) => (
                    <div key={comment._id}>
                      <img src={comment.userimage} width="100px" />

                      <h2>{comment.username}</h2>

                      <h1>{comment.content}</h1>

                      {/* <Link to={`/blogviews/${value._id}`} activeClassName="current"><h5>View</h5></Link> */}
                      {/* yeh code direct connect hai ek funtion se */}


                    </div>

                  ))}

                </div>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                {/* <button type="button" className="btn btn-primary">refresh</button> */}
              </div>
            </div>

          </div>

        </div>
        </div>
      </>
      )}
      <ToastContainer/>
    </>
  )
}

export default Textposts