import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import '../style/uploadblog.css';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Uploadblogs = () => {
  const [title, settitle] = useState('');
  const [description, setdescription] = useState('');
  const [userid, setuserid] = useState('');
  const [username, setusername] = useState('');
  const [Loadingimg, setloadingimg] = useState(false)
  const [imguser, setimguser] = useState('');
  const [imgurl, setimgurl] = useState('')
  const navigate = useNavigate();



  // login check karta hai
  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get('/dashboard')
        console.log(response.data.user);
        if (response.data.user === "error") {
          navigate('/', { replace: true })


        }

      } catch (error) {
        toast.error('Something is Wrong!', {
          position: "top-center"
      })
      }

    }
    getUser()


  }


    ,[])
    
    const loadFile = function (e) {
      var reader = new FileReader();
      reader.onload = function () {
          var output = document.getElementById('output');
          output.src = reader.result;
      };
      reader.readAsDataURL(e.target.files[0]);
  };

  useEffect(() => {
    window.addEventListener("offline", (e) => {
      toast.info('you are offline.', {
        position: "bottom-center"
    })
    
    });
    
    window.addEventListener("online", (e) => {
      toast.info('you are online.', {
        position: "bottom-center"
    })

    });
    
  },[])


  useEffect(() => {
    async function getUser() {
      try {
        const response = await axios.get(`/uploadblog`)
        console.log(response.data.user._id);
        setuserid(response.data.user._id)
        setusername(response.data.user.name)

      } catch (error) {
        toast.error('Something is Wrong!', {
          position: "top-center"
      })
      }

    }
    getUser()


  }


    , [])


  const okay = async () => {
    setloadingimg(true)
    toast.success('Image is uploaded Successfully.', {
      position: "top-center"
  })
    try {
      const data = new FormData();
      data.append("file", imguser)
      data.append("upload_preset", "arextracloud")
      data.append("cloud_name", "arextrabook")


      fetch('https://api.cloudinary.com/v1_1/arextrabook/image/upload', {
        method: "post",
        body: data,

      }).then((res) => res.json())
        .then(data => {
          setimgurl(data.url)
          setloadingimg(false)
         
        })
        .catch(err => {toast.error('Something is Wrong!', {
          position: "top-center"
      })})

    } catch (error) {
      toast.error('Something is Wrong!', {
        position: "top-center"
    })
    }
  }

  let submit = async (e) => {
    e.preventDefault()
    if (imgurl === "") {
      toast.warn('Image is not uploaded.', {
        position: "top-center"
    })
    } else {
      
      // console.log(user)
      try {
        console.log(imgurl + "iifhrur")
        await axios.post('/uploadblog', {
          title: title,
          description: description,
          userid: userid,
          username: username,
          image: imgurl

        }

        )
        toast.success('Blog is uploaded Successfully.', {
          position: "top-center"
      })
      } catch (error) {
        toast.error('Something is Wrong!', {
          position: "top-center"
      })
      }
    }

  }
  const Isloading = () => {
    return (
      <>
        <div class="d-flex justify-content-center">
          <div class="spinner-border text-primary" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>
      </>
    )
  }

  return (
    <>
      {Loadingimg ? <Isloading /> : (<>
        <h1 style={{ marginLeft: '80px' }}>AR ExtraBook</h1>
        <label htmlFor="" className='upllabel'>Image</label><br />

        <i
          style={{ fontSize: '50px', marginLeft: '150px' }}

          className="fa fa-file-image-o"
          data-bs-toggle="modal"
          data-bs-target="#exampleModal"
        >
        </i><hr />

        <form>
          <label htmlFor="" className='upllabel'>Title</label>
          <input type="text" name="title" onChange={(e) => { settitle(e.target.value) }} id="uplinput" required /><br />
          <label htmlFor="" className='upllabel'>Description</label>

          <input type="text" name="description" onChange={(e) => { setdescription(e.target.value) }} id="uplinput" required /><hr />
          {/* <input type="file" name="image" onChange={(e) => { setimguser(e.target.files[0]) }} id="" required /><br /> */}



          <input type="submit" className='btn btn-primary' style={{ width: '200px', marginLeft: '70PX', marginTop:'10px' }} onClick={submit} value="Upload" id="" />
        </form>
        <div
                            className="modal fade"
                            id="exampleModal"
                            tabIndex={-1}
                            aria-labelledby="exampleModalLabel"
                            aria-hidden="true"
                        >
                            <div className="modal-dialog">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="exampleModalLabel">
                                            upload Image
                                        </h5>
                                        <button
                                            type="button"
                                            className="btn-close"
                                            data-bs-dismiss="modal"
                                            aria-label="Close"
                                        />
                                    </div>
                                    <div className="modal-body">
                                        <img id='output' style={{ width: '100px' }} />
                                        <input type="file" className='custom-file-input' name="image" onChange={(e) => {
                                            loadFile(e)
                                            setimguser(e.target.files[0])
                                        }} id="" required /><br />


                                    </div>
                                    <div className="modal-footer">
                                        <button
                                            type="button"
                                            className="btn btn-danger"
                                            data-bs-dismiss="modal"
                                        >
                                            Cancel
                                        </button>
                                        <button type="button" className="btn btn-success" onClick={okay} data-bs-dismiss="modal" aria-label="Close" >
                                            Upload
                                        </button>
                                    </div>
                                </div>
                            </div>
                        </div>
      </>)}
    <ToastContainer/>
    </>
  )
}

export default Uploadblogs